.counter-container {
  display: flex; /* Use Flexbox for easy responsiveness */
  /* Allow items to wrap to new lines */
  justify-content: center; /* Center items horizontally */
  gap: 20px; /* Space between counters */
  padding: 20px;
  flex-direction: row;
  align-items: center;
}

.counter {
  text-align: center; /* Center content within each counter */
  flex: 1; /* Allow counters to grow/shrink evenly */
  min-width: 200px; /* Minimum width for each counter */
  padding: 20px;
  border: 1px solid #ddd; /* Add a subtle border */
  border-radius: 8px; /* Round the corners slightly */
}
.count {
  font-size: 2.5rem; /* Make the count number larger */
  margin-bottom: 10px;
}
@media (max-width: 768px) {
  /* Tablet and smaller screens */
  .counter-container {
    flex-direction: column; /* Stack counters vertically on smaller screens */
    align-items: center;
  }

  .counter {
    min-width: 90%; /* Adjust minimum width for smaller screens */
    margin-bottom: 20px;
  }
}

@media (max-width: 480px) {
  /* Mobile phones */
  .count {
    font-size: 2rem; /* Further adjust font size for mobile */
  }

  .counter-container {
    gap: 10px; /* Smaller gap between counters on mobile */
  }
}
